.ProductDetails {
  background-color: rgb(255, 255, 255);
  width: 100vw;
  max-width: 100%;
  padding: 0 6vmax;
  margin: 1vmax 0;
  box-sizing: border-box;
  display: flex;
}

.ProductDetails > div {
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
  padding: 2vmax;
  box-sizing: border-box;
  border: 1px solid white;
}

.ProductDetails > div:last-child {
  align-items: flex-start;
}

.CarouselImage {
  width: 22vmax;
}

.detailsBlock-1 > h2 {
  color: rgb(54, 54, 54);
  font: 400 1.6vmax "Roboto Slab";
}

.detailsBlock-1 > p {
  color: rgba(54, 54, 54, 0.582);
  font: 200 0.6vmax;
}

/* .detailsBlock-2 {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  border-top: 1px solid rgba(0, 0, 0, 0.205);
  border-bottom: 1px solid rgba(0, 0, 0, 0.205);
  width: 70%;
  padding: 1vmax 0;
} */

.detailsBlock-2-span {
  font: 200 0.8vmax;
  color: rgba(0, 0, 0, 0.699);
}

.detailsBlock-3 {
  width: 70%;
}

.priceInfo > h1 {
  color: rgb(128, 0, 57);
  font: 400 1.6vmax "Roboto Slab";
  margin: 1vmax 0;
}

.priceInfo > h3 {
  color: rgba(17, 17, 17, 0.795);
  font: 300 1vmax "Franklin Gothic Medium";
  margin: 1.6vmax 0;
  text-decoration: line-through;
}

.priceInfoDiscount {
  color: green;
  font: 400 1.2vmax "Franklin Gothic Medium";
  margin: 1.4vmax 0;
}

.priceInfo {
  display: flex;
  gap: 1vmax;
  border-top: 1px solid rgba(0, 0, 0, 0.205);
}

.detailsBlock-3-1 {
  display: flex;
  align-items: center;
}

.detailsBlock-3-1-1 > button {
  border: none;
  background-color: rgba(0, 0, 0, 0.616);
  padding: 0.5vmax;
  cursor: pointer;
  color: white;
  transition: all 0.5s;
}
.detailsBlock-3-1-1 > button:hover {
  background-color: rgba(0, 0, 0, 0.767);
}

.detailsBlock-3-1-1 > input {
  border: none;
  padding: 0.5vmax;
  width: 2vmax;
  text-align: center;
  outline: none;
  font: 400 0.8vmax "Roboto Slab";
  color: rgba(0, 0, 0, 0.74);
}

.detailsBlock-3-1 > button:last-child {
  border: none;
  cursor: pointer;
  color: white;
  transition: all 0.5s;
  background-color: rgb(128, 0, 57);
  font: 400 0.8vmax "Roboto Slab";
  border-radius: 0;
  padding: 0.5vmax 1vmax;
  margin: 1vmax 2vmax;
  outline: none;
}

.detailsBlock-3-1 > button:last-child:hover {
  background-color: #500024;
}

.detailsBlock-3 > p {
  border-top: 1px solid rgba(0, 0, 0, 0.205);
  border-bottom: 1px solid rgba(0, 0, 0, 0.205);
  padding: 1vmax 0;
  color: #303030;
  font: 400 1.2vmax "Roboto Slab";
  margin: 1vmax 0vmax;
}

.detailsBlock-3 > p > span {
  font: 400 1.2vmax "Arial";
}

.detailsBlock-4 {
  color: #303030;
  font: 400 1.2vmax "Roboto Slab";
}

.productDescription {
  color: #707070;
  font: 300 0.8vmax "Roboto Slab";
}

.submitReview {
  border: none;
  background-color: rgb(128, 0, 57);
  font: 400 0.8vmax "Roboto Slab";
  border-radius: 0;
  padding: 0.5vmax 1.5vmax;
  margin: 2vmax 0;
  color: white;
  cursor: pointer;
  transition: all 0.5s;
  outline: none;
}
.submitReview:hover {
  background-color: #500024;
}

.submitDialog {
  display: flex;
  flex-direction: column;
}

.submitDialogTextArea {
  border: 1px solid rgba(0, 0, 0, 0.082);
  margin: 1vmax 0;
  outline: none;
  padding: 1rem;
  font: 300 1rem "Roboto Slab";
}

.reviewsHeading {
  color: #000000be;
  font: 500 1.4vmax "Roboto Slab";
  text-align: center;
  border-bottom: 1px solid rgba(0, 0, 0, 0.226);
  padding: 1vmax;
  width: 20vmax;
  margin: auto;
  margin-bottom: 4vmax;
}
.reviews {
  display: flex;
  overflow: auto;
}

.reviewCard {
  flex: none;

  box-shadow: 0 0 5px rgba(0, 0, 0, 0.226);
  border: 1px solid rgba(56, 56, 56, 0.116);
  width: 30vmax;
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: 1vmax;
  padding: 3vmax;
}
.reviewCard > img {
  width: 5vmax;
}
.reviewCard > p {
  color: rgba(0, 0, 0, 0.836);
  font: 600 0.9vmax "Roboto Slab";
}
.reviewCardComment {
  color: rgba(0, 0, 0, 0.445);
  font: 300 0.8vmax;
}

.noReviews {
  font: 400 1.3vmax "Gill Sans";
  text-align: center;
  color: rgba(0, 0, 0, 0.548);
}

@media screen and (max-width: 600px) {
  .ProductDetails {
    flex-direction: column;
    height: unset;
  }
  .ProductDetails > div {
    padding: 0.5vmax;
  }

  .ProductDetails > div:last-child {
    align-items: center;
  }

  .detailsBlock-1 > h2 {
    font-size: 2.2vmax;
    text-align: center;
  }

  .detailsBlock-1 > p {
    text-align: center;
    font-size: 1vmax;
  }

  .detailsBlock-2 {
    justify-content: center;
  }
  .detailsBlock-2 > span {
    font-size: 1.5vmax;
  }

  .priceInfo > h1 {
    font: 700 2vmax "Franklin Gothic Medium";
  }

  .priceInfo {
    justify-content: center;
  }

  /* .detailsBlock-3 > h1 {
  } */

  .detailsBlock-3-1 {
    flex-direction: column;
  }

  .detailsBlock-3-1-1 {
    padding: 0.5vmax 0;
  }
  .detailsBlock-3-1-1 > button {
    padding: 1.2vmax;
    width: 4vmax;
  }

  .detailsBlock-3-1-1 > input {
    padding: 1.5vmax;
    width: 5vmax;
    font: 400 1.8vmax "Roboto Slab";
  }

  .detailsBlock-3-1 > button:last-child {
    font: 500 1.7vmax "Roboto Slab";
    padding: 1.5vmax;
    width: 20vmax;
    margin: 1vmax 0;
  }

  .detailsBlock-3 > p {
    padding: 2.5vmax 0;
    text-align: center;
    font: 400 2vmax "Roboto Slab";
  }

  .detailsBlock-3 > p > span {
    font: 400 2vmax "Arial";
  }

  .CarouselImage {
    width: 36vmax;
  }


  .detailsBlock-4 {
    font-size: 2vmax;
    font-weight: 400;
  }

  .productDescription {
    font: 300 1.8vmax "Roboto Slab";
  }

  .submitReview {
    font: 500 1.7vmax "Roboto Slab";
    padding: 1.5vmax;
    width: 20vmax;
    margin: 3vmax 0;
  }

  .reviewsHeading {
    font: 600 2vmax "Roboto Slab";
}

  .reviewCard > p {
    font: 600 3vw "Roboto Slab";
  }
  .reviewCardComment {
    font: 300 5vw;
  }
}
